import { DEFAULT_LINE_ICON } from "@/features/navigation/data"

export function createLineSwitcherOptions(lineIds = []) {
  return lineIds
    .filter((lineId) => typeof lineId === "string" && lineId.trim().length > 0)
    .map((lineId) => {
      const trimmed = lineId.trim()
      return {
        id: trimmed,
        name: trimmed,
        description: "Production line",
        icon: DEFAULT_LINE_ICON,
      }
    })
}
